
<template>
<div style="height: 100%"> 
   
    <el-row>
  <el-col :span="6"><div class="grid-content bg-purple qryitem">
    
  <el-input
    placeholder=""
    v-model="nameInput">
  </el-input>
  </div></el-col>
  <!-- <el-col :span="6"><div class="grid-content bg-purple-light qryitem">
    
      <el-select v-model="qryStatus" placeholder="">
    <el-option
      v-for="item in statusOptions"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
  </div></el-col> -->
  <el-col :span="6"><div class="grid-content bg-purple qryitem">
     
  <el-input
    placeholder=""
    v-model="typeNameInput">
  </el-input>
   </div></el-col>
  <el-col :span="4"><div class="grid-content bg-purple-dark">
  <el-button type="primary" icon="el-icon-search" @click="qryData" ></el-button>
  <el-button type="success" icon="el-icon-circle-plus-outline" @click="add" ></el-button>
  </div>
  </el-col>
</el-row>
<div style="height: 100%">
  
    <el-table
            v-loading="loading"
            :data="tableData"
             height="calc(100% - 110px)"
            style="width: 100%">
            <el-table-column  type="index" label=""  width="50"></el-table-column>
          <el-table-column prop="id"  label=""  width="180"></el-table-column>
        <el-table-column  prop="name"  label=""  width="180"></el-table-column>
        <el-table-column  prop="type"  label=""  width="180"></el-table-column>
        <el-table-column  prop="num"  label=""  ></el-table-column>
        <el-table-column  operation=""  label="">
            <template scope="scope">
                <el-button  size="small"  @click="imgexport(scope.$index, scope.row)"> </el-button>
            </template>
        </el-table-column>
        <!-- <el-table-column  operation=""  label="">
            <template scope="scope">
                <el-button  size="small"  type="danger"  @click="handleDelete(scope.$index, scope.row)"></el-button>
                <el-button  size="small"  @click="showQRCode(scope.$index, scope.row)"> </el-button>
                <el-button  size="small"  @click="generateQRCode(scope.$index, scope.row)"> </el-button>
            </template>
        </el-table-column> -->
    </el-table>
    <div class="Pagination" style="text-align: left;margin-top: 10px;">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-size="5"
                  layout="total, prev, pager, next"
                  :total="count">
                </el-pagination>
            </div>
</div>
<el-dialog title="" width="500px" top="20px" :visible.sync="dialogFormVisible">
  <el-form :model="form" :rules="rules2"  ref="form">
      <el-form-item label="" :label-width="formLabelWidth">
                <el-upload
                class="avatar-uploader"
                action="/wish/file/uploadImg"
                :show-file-list="false"
                swith-credentials ="true"
                :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </el-form-item>
       <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.type" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
                    <el-input-number v-model="form.num" :min="0" label=""></el-input-number>
    </el-form-item>
 <el-form-item label="" :label-width="formLabelWidth">
                  <el-date-picker
      v-model="form.startTime"
      type="date"
      placeholder="">
    </el-date-picker>
</el-form-item>
    <el-form-item label="" :label-width="formLabelWidth">
                  <el-date-picker
      v-model="form.endTime"
      type="date"
      placeholder="">
    </el-date-picker>
</el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible = false"> </el-button>
    <!-- <el-button @click="resetForm('form')"> </el-button> -->
    <el-button type="primary" @click="submitForm('form')"> </el-button>
    
  </div>
</el-dialog>


<el-dialog
  title=""
  :visible.sync="qrCodeVisible"
  width='270px'>
      <img src="http://element-cn.eleme.io/static/hamburger.50e4091.png" class="image">
  <span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="qrCodeVisible = false"> </el-button>
  </span>
</el-dialog>

</div>
    </template>
<script type="text/ecmascript-6">
export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (this.form.checkPass !== "") {
          this.$refs.form.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.form.password) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };

    return {
      form: {
        id: "",
        name: "",
        type: "",
        num: "",
        startTime: "",
        endTime: "",
        img:""
      },
      nameInput: "",
      typeNameInput:"",
      formLabelWidth: "50px", //label
      dialogFormVisible: false, //
      qrCodeVisible:false,
      tableData: [],
      formLabelWidth: "120px",
      imageUrl: "",
      imgprefixUrl:'https://139.224.114.192:8080/upload/',
      loading: true,
      count: 0,
      currentPage: 1,
      rules2: {
        password: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }]
      },
      qryStatus:'',
      statusOptions: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }, {
          value: '4',
          label: ''
        }],
    };
  },
  created() {
    this.initData();

    this.loading = false;
  },
  methods: {
    imgexport: function(index, row){
      var url = 'https://www.xingyuanji.com/wish/file/export?path=' + row.zipPath;
      window.open(url);
    },
    handleAvatarSuccess(res, file) {
      
      if(0 == res.result){
        this.form.img  = res.data;
        this.imageUrl = URL.createObjectURL(file.raw);
      }else{
          this.$message.error(res.msg);
      }
      
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error(' JPG !');
      }
      if (!isLt2M) {
        this.$message.error(' 2MB!');
      }
      return isJPG && isLt2M;
    },
    //
    initData() {
      var _this = this;
    },
    qryData() {
      var that = this;
       var qryName = that.nameInput;
       var qryType = that.typeNameInput;
      if(!that.nameInput){
        qryName = null;
      }
     if(! that.typeNameInput){
        qryType = null;
      }

      var qryData = {
        page: {
          pageNum: that.currentPage,
          pageSize: 5
        },
        condition: {
           name: qryName
           ,type: qryType
        }
      };
      console.info(qryData);
      that.$axios.prizeQry(qryData).then(
        function(res) {
          //
          console.info(res);
          that.tableData = res.data.list;
          that.currentPage = res.data.pageNum;
          that.count = res.data.total;
        },
        function(res) {
          //
          console.info("error");
          console.info(res);
          _this.$message({
              message:res.msg,
              type: "warning"
            });
        }
      );
    },
    formatterSexColumn(row, column) {
      switch (row.sex) {
        case 0:
          return "";
          break;
        case 1:
          return "";
          break;
        default:
          return "";
      }
    },
    handleSizeChange(val) {
      //console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.qryData();
    },
    resetForm(formName) {
      var that = this;
      that.$refs[formName].resetFields();
      this.form.id = null;
      this.form.name = null;
      this.form.type = null;
      this.form.num = null;
      this.form.startTime = null;
      this.form.endTime = null;
      this.form.img = null;
    },
    submitForm(formName) {
      var _this = this;
      _this.$refs[formName].validate(valid => {
        if (valid) {
          //alert('submit!');
          console.info(_this.form.id);
          if (!_this.form.id) {
            _this.$axios.savePrize(_this.form).then(
              function(res) {
                //
                console.info(res);
                if (0 == res.result) {
                  _this.$message({
                    message: "",
                    type: "success"
                  });
                  _this.dialogFormVisible = false;
                  _this.resetForm('form');
                  _this.qryData();
                }
              },
              function(res) {
                //
                console.info("error");
                console.info(res);
                _this.$message({
                  message: "",
                  type: "warning"
                });
              }
            );
          } else {
            _this.$axios.updateUser(_this.form).then(
              function(res) {
                //
                console.info(res);
                if (0 == res.result) {
                  _this.$message({
                    message: "",
                    type: "success"
                  });
                  _this.dialogFormVisible = false;
                  _this.resetForm('form');
                  _this.qryData();
                }
              },
              function(res) {
                //
                console.info("error");
                console.info(res);
                _this.$message({
                  message: ","+JSON.stringify(res),
                  type: "warning"
                });
              }
            );
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //
    add() {
      this.dialogFormVisible = true;
      this.resetForm("form");
    },
    //
    handleDelete(index, row) {
      //this.tableData.splice(index, 1);
      var _this = this;
      var data=[row.id];
      console.info(data);
      _this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
         _this.$axios.delPrize(data).then(
              function(res) {
                //
                console.info(res);
                if (0 == res.result) {
                  _this.$message({
                    message: "",
                    type: "success"
                  });
                  _this.dialogFormVisible = false;
                  //_this.resetForm("form");
                  _this.qryData();
                }
              },
              function(res) {
                //
                console.info("error");
                console.info(res);
                _this.$message({
                  message: "",
                  type: "warning"
                });
              }
            );
        }).catch(() => {
                
        });
      

      
    },
    //
    modify(index, row) {
      var that = this;
      that.dialogFormVisible = true;
      console.info(row);
      that.form.id = row.id;
      this.form.name = row.name;;
      this.form.type = row.type;;
      this.form.num = row.num;;
      this.form.startTime = row.startTime;;
      this.form.endTime = row.endTime;;
    },
    generateQRCode(index, row){
            var _this = this;
      var data=[row.id];
      console.info(data);
      _this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
         _this.$axios.generateQRCode(row.id).then(
              function(res) {
                //
                console.info(res);
                if (0 == res.result) {
                  _this.$message({
                    message: "",
                    type: "success"
                  });
                  _this.dialogFormVisible = false;
                  //_this.resetForm("form");
                  _this.qryData();
                }
              },
              function(res) {
                //
                console.info("error");
                console.info(res);
                _this.$message({
                  message: "",
                  type: "warning"
                });
              }
            );
        }).catch(() => {
                
        });
    },
    showQRCode(index, row){
           var _this = this;
           var data=[row.id];
            _this.$axios.prizeDetail(row.id).then(
              function(res) {
                //
                console.info(res);
                if (0 == res.result) {
                  _this.qrCodeVisible = true;
                }
              },
              function(res) {
                //
                console.info("error");
                console.info(res);
                _this.$message({
                  message: "",
                  type: "warning"
                });
              }
            );
    },
    // handleAvatarSuccess(res, file) {
    //   this.imageUrl = URL.createObjectURL(file.raw);
    // },
    // beforeAvatarUpload(file) {
    //   const isJPG = file.type === "image/jpeg";
    //   const isLt2M = file.size / 1024 / 1024 < 2;

    //   if (!isJPG) {
    //     this.$message.error(" JPG !");
    //   }
    //   if (!isLt2M) {
    //     this.$message.error(" 2MB!");
    //   }
    //   return isJPG && isLt2M;
    // }
  }
};
</script>
<style>
.el-row {
  margin-bottom: 20px;
}
.el-input {
  width: 180px;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 80px;
  height: 80px;
  line-height: 80px;
  text-align: center;
}
.avatar {
  width: 80px;
  height: 80px;
  display: block;
}
 .time {
    font-size: 13px;
    color: #999;
  }
  
  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 0;
    float: right;
  }

  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }
  
  .clearfix:after {
      clear: both
  }
</style>


