
<template>
<div style="height: 100%;">
   <el-row>
<!--    
   
      <el-col :span="6"><div class="grid-content bg-purple qryitem">
  
  <el-input
    placeholder=""
    v-model="nameInput">
  </el-input>
  </div></el-col>
  <el-col :span="6"><div class="grid-content bg-purple-light qryitem">
    
      <el-select v-model="statusInput" placeholder="">
    <el-option
      v-for="item in statusData"
      :key="item.id"
      :label="item.description"
      :value="item.id">
    </el-option>
  </el-select>
  </div></el-col> -->
  <el-col :span="4"><div class="grid-content bg-purple-dark">
  <el-button type="primary" icon="el-icon-search" @click="qryData" ></el-button>
  <el-button type="success" icon="el-icon-circle-plus-outline" @click="add" ></el-button>
  </div>
  </el-col>
</el-row>
<div style="height: 100%">
    <el-table
            v-loading="loading"
            :data="tableData"
            height="calc(100% - 110px)"
            style="width: 100%">
            <el-table-column  type="index" label=""  width="50"></el-table-column>
        <el-table-column prop="realName"  label=""  width="180"></el-table-column>
        <el-table-column  prop="viewOrder"  label=""  width="180"></el-table-column>
        <el-table-column  prop="status" :formatter="formatterStatusColumn" width="180" label=""></el-table-column>
        <el-table-column  operation=""  label="">
            <template scope="scope">
                <el-button  size="small"  @click="modify(scope.$index, scope.row)"> </el-button>
                <el-button  size="small"  @click="showImg(scope.$index, scope.row)"> </el-button>
                <!-- <el-button  size="small"  type="danger"  @click="handleDelete(scope.$index, scope.row)"></el-button> -->
            </template>
        </el-table-column>
    </el-table>
    <div class="Pagination" style="text-align: left;margin-top: 10px;">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-size="5"
                  layout="total, prev, pager, next"
                  :total="count">
                </el-pagination>
            </div>
</div>
<el-dialog title="" width="500px" top="20px" :visible.sync="dialogFormVisible">
  <el-form :model="form" :rules="rules2"  ref="form">
    <el-form-item label="" :label-width="formLabelWidth">
                <el-upload
        class="avatar-uploader"
        action="/wish/file/uploadImg"
        :show-file-list="false"
        :with-credentials ="credentials"
        :on-success="handleAvatarSuccess"
        :before-upload="beforeAvatarUpload">
        <img v-if="imageUrl" :src="imageUrl" class="avatar">
        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
       <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.realName" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
             <el-input-number v-model="form.viewOrder" :min="0" label=""></el-input-number>
        </el-form-item>
    <el-form-item label="" :label-width="formLabelWidth">
         <el-select v-model="form.status" placeholder="">
    <el-option
      v-for="item in statusData"
      :key="item.id"
      :label="item.description"
      :value="item.id">
    </el-option>
  </el-select>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible = false"> </el-button>
    <!-- <el-button @click="resetForm('form')"> </el-button> -->
    <el-button type="primary" @click="submitForm('form')"> </el-button>
    
  </div>
</el-dialog>

<el-dialog
  title=""
  :visible.sync="imgVisible">
      <img :src="imgShowPath" class="image"  width='570px'>
  <span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="imgVisible = false"> </el-button>
  </span>
</el-dialog>
</div>
    </template>
<script type="text/ecmascript-6">
export default {
  data() {
    return {
      form: {
        id: "",
        realName: "",
        viewOrder: "",
        status: "",
        imgPath:""
      },
      nameInput: "",
      statusInput: "",
      formLabelWidth: "50px", //label
      dialogFormVisible: false, //
      imgVisible:false,
      imgShowPath:"",
      imageUrl:"",
      tableData: [],
      formLabelWidth: "120px",
      loading: true,
      credentials:true,
      count: 0,
      currentPage: 1,
      imgprefixUrl:'https://www.xingyuanji.com/upload/',
      statusData: [],
       rules2: {
    //     password: [{ validator: validatePass, trigger: "blur" }],
    //     checkPass: [{ validator: validatePass2, trigger: "blur" }]
       }
    };
  },
  created() {
    this.initData();

    this.loading = false;
  },
  methods: {
    //
    initData() {
      var _this = this;

      _this.statusData = [
        {
          id: 1,
          description: ""
        },
        {
          id: 2,
          description: ""
        }
      ];
    },
    qryData() {
      var that = this;
      var qryName = that.nameInput;
       var qryStatus = that.statusInput;
      if(!qryName){
        qryName = null;
      }
     if(! qryStatus){
        qryStatus = null;
      }

      var qryData = {
        page: {
          pageNum: that.currentPage,
          pageSize: 5
        },
        condition: {
          realName: qryName,
          status:qryStatus
        }
      };
      console.info(qryData);
      that.$axios.findAllCycleImg(qryData).then(
        function(res) {
          //
          console.info(res);
          that.tableData = res.list;
        //   that.currentPage = res.data.pageNum;
       //     that.count = res.data.total;
        },
        function(res) {
          //
          console.info("error");
          console.info(res);
          _this.$message({
              message:res.msg,
              type: "warning"
            });
        }
      );
    },
    formatterStatusColumn(row, column) {
      switch (row.status) {
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        default:
          return "";
      }
    },
    showImg(index, row){
      this.imgShowPath = this.imgprefixUrl+row.imgPath;
      this.imgVisible = true;
    },
    handleSizeChange(val) {
      //console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.qryData();
    },
    resetForm(formName) {
      var that = this;
      that.$refs[formName].resetFields();
      this.form.id = null;
      this.form.realName = null;
      this.form.status = null;
      this.form.imgPath = null;
      this.imageUrl = null;
    },
    submitForm(formName) {
      var _this = this;
      _this.$refs[formName].validate(valid => {
        if (valid) {
          //alert('submit!');
          _this.form.img = _this.imageUrl;
          console.info(_this.form.id);
          if (!_this.form.id) {
            _this.$axios.saveCycleImg(_this.form).then(
              function(res) {
                //
                console.info(res);
                if (0 == res.result) {
                  _this.$message({
                    message: "",
                    type: "success"
                  });
                  _this.dialogFormVisible = false;
                  _this.resetForm("form");
                  _this.qryData();
                }
              },
              function(res) {
                //
                console.info("error");
                console.info(res);
                _this.$message({
                  message: "",
                  type: "warning"
                });
              }
            );
          } else {
            _this.$axios.updateCycleImg(_this.form).then(
              function(res) {
                //
                console.info(res);
                if (0 == res.result) {
                  _this.$message({
                    message: "",
                    type: "success"
                  });
                  _this.dialogFormVisible = false;
                  _this.resetForm("form");
                  _this.qryData();
                }
              },
              function(res) {
                //
                console.info("error");
                console.info(res);
                _this.$message({
                  message: "",
                  type: "warning"
                });
              }
            );
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //
    add() {
      this.dialogFormVisible = true;
      this.resetForm("form");
    },
    //
    handleDelete(index, row) {
      //this.tableData.splice(index, 1);
      var _this = this;
      var data = [row.id];
      console.info(data);
      _this
        .$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
        .then(() => {
          _this.$axios.delUser(data).then(
            function(res) {
              //
              console.info(res);
              if (0 == res.result) {
                _this.$message({
                  message: "",
                  type: "success"
                });
                _this.dialogFormVisible = false;
                //_this.resetForm("form");
                _this.qryData();
              }
            },
            function(res) {
              //
              console.info("error");
              console.info(res);
              _this.$message({
                message: "",
                type: "warning"
              });
            }
          );
        })
        .catch(() => {});
    },
    //
    modify(index, row) {
      var that = this;

      that.dialogFormVisible = true;
      console.info(row);
      that.form.id = row.id;
      that.form.realName = row.realName;
      that.form.viewOrder = row.viewOrder;
      that.form.status = row.status;
      that.form.imgPath = row.imgPath;
      that.imageUrl = that.imgprefixUrl+row.imgPath;
    },
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
      console.info('this.imageUrl:'+this.imageUrl);
      console.info(res);
      if(0 == res.result){
        this.form.imgPath  = res.data;
      }else{
          this.$message.error("!");
      }
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error(" JPG !");
      }
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return isJPG && isLt2M;
    }
  }
};
</script>
<style>
.el-row {
  margin-bottom: 20px;
}
.el-input {
  width: 180px;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 80px;
  height: 80px;
  line-height: 80px;
  text-align: center;
}
.avatar {
  width: 80px;
  height: 80px;
  display: block;
}
</style>


